<template>
  <el-container>
    <el-header>
      <el-menu :default-active="$route.path" class="el-menu-demo" mode="horizontal" :router=true>
        <el-menu-item index="/index">我的工作台</el-menu-item>
        <el-menu-item index="/setting">账号设置</el-menu-item>
        <el-menu-item index="/logout">当前账户：{{username}} 注销</el-menu-item>
      </el-menu>
    </el-header>
    <el-main>
      <router-view/>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: 'workspace',
  data: function () {
    return {
      username: sessionStorage.getItem('accessToken')
    }
  },
  methods: {
    skip (a) {
      this.$router.push(a)
    }
  }
}
</script>

<style scoped>

</style>



